import React , {Component} from 'react'
import { Link } from 'react-router-dom'
import NavIcon from './NavIcon'


class NavBar extends Component{
    render(){
        var styles = {
            backgroundColor : "#08080840"
        }
        return (
            <nav class="navbar navbar-expand-lg navbar-light">
                {/* <div class="navbar-header">
                    <Link to="/"> <div className="brand-text">Portfolio</div></Link>
                </div>
                 */}
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>

                <div class="collapse navbar-collapse" id="navbarSupportedContent">
                    <ul class="navbar-nav ml-auto">
                        <li class="nav-item mx-3 active">
                            <Link class="nav-link" to="/home">
                                <NavIcon src='home.png' text='Home'/>
                            </Link>
                        </li>
                        <li class="nav-item mx-3 active">
                            <Link class="nav-link" to="/gallery">
                                <NavIcon src='gallery.png' text='Gallery'/>
                            </Link>
                        </li> 
                        <li class="nav-item mx-3 active">
                            <Link class="nav-link" to="/contact">
                                <NavIcon src='card.png' text='Contact'/>
                            </Link>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}

export default NavBar;